

// 使footer置底 start
html,body{
    height: 100%;
    margin: 0;
}
body {
    display: flex; /*使物件依序排列*/
    flex-direction: column; /*使物件垂直排列*/
  }
main{
        flex-grow: 1; /*可佔滿垂直剩餘的空間*/
        background-color: $body-bg;
        margin-top: 72px;
}
// 使footer置底 end





body{
  
    font-weight: 400;
    line-height: 1.5;
    scroll-behavior: smooth;
}


a{
    text-decoration: none;
    color: inherit;
    display: block;
}     
