img{
    max-width: 100%;
    height: auto;
    display: block;
}

h1, h2, h3, h4, h5, h6,ul,ol,p{
    margin: 0;
    line-height: 1.2;
}

ol, ul {
	list-style: none;
    padding-left: 0;
}








