.wrap{
    margin-top: 72px;
}

input{
    // 取消ios預設樣式
    appearance: none;
}
input[type="checkbox"]{
    appearance: checkbox;
}

input[type="radio"]{
    appearance: radio;
}


.material-symbols-outlined {
  font-variation-settings:
  'FILL' 0,
  'wght' 400,
  'GRAD' 0,
  'opsz' 48
}



