  margin-left: 48px;
}

.radio-list {
  margin-bottom: 8px;
}

.btn-order {
  width: 170px;
  float: right;
}

.safe {
  margin-bottom: 24px;
}

.backToProduct {
  display: flex;
  align-items: center;
}

.backToProduct a:nth-last-child(1) {
  margin-left: 4px;
}

.cart-title {
  padding: 16px 0px;
}

.line-cart {
  height: 1px;
  width: 100%;
  background-color: rgba(48, 48, 48, 0.1);
}

.cart-item {
  display: flex;
  padding: 24px 0px;
  border-bottom: 1px solid rgba(48, 48, 48, 0.1);
}

.cart-item > img {
  width: 80px;
}

.item-main {
  width: 100%;
}

.item-name, .item-price {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 12px;
}

.item-price {
  height: 100%;
}

.item-name > img {
  cursor: pointer;
}

.number-cart {
  width: 74px;
  padding: 8px 16px;
  border-radius: 4px;
  border: 1px solid rgba(48, 48, 48, 0.1);
  background-color: #68715F;
}

.cost {
  margin-top: 24px;
}

.cost li {
  display: flex;
  justify-content: space-between;
}

.cost .subTotal {
  margin-bottom: 16px;
}

.cost .deliveryFree {
  padding-bottom: 24px;
  border-bottom: 1px solid rgba(48, 48, 48, 0.1);
}

.cost .total {
  margin-top: 24px;
}

.input-delivery {
  margin-bottom: 24px;
}

@media (max-width: 767px) {
  .order-title {
    font-size: 24px;
  }
  .shopping-main {
    flex-direction: column-reverse;
  }
  .shopping-main form {
    box-shadow: none;
  }
  .order, .cart {
    width: 100%;
  }
  .cart {
    margin-left: 0px;
  }
  .btn-order {
    width: 100%;
  }
}
